body {
    font-family: Arial, Helvetica, sans-serif;
}

/* Container */
#container {
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    width: 100%;
}

/* Navigation */

.navigation {
    position: fixed;
    top: 0;
    left: 0;

    height: auto;
    width: 100%;

    margin: 0;

    background-color: #282c34;

    font-size: smaller;
    color: #ffffff;
}

.navigation ul {
    height: 100%;

    list-style: none;

    padding-left: 1%;
    margin: 0;
}

.navigation li {
    display: inline-block;

    height: 100%;

    padding-left: 3%;
    padding-right: 3%;

    font-size: 3vmin;
    font-weight: bold;

    transition: 0.5s;
}

.navigation li:hover{

    color: #000000;
    background-color: #ffffff;
}

.navigation li:hover a{
    color: #000000;

    cursor: pointer;
}

.navigation li a {
    text-decoration: none;
    color: #ffffff
}

/* Content */

.content {
    position: absolute;

    height: auto;
    width: 100%;
}


/* Card */

.card{
    margin:0 ;

    text-align: center;
}